import React from "react";
import Chart from "react-apexcharts";

import Container from "react-bootstrap/Container";

function LineExportImportPortfolio(props) {

    let series = [
        { name: 'Оценка Экспортера', data: [100, 120, 120, 140, 140, 160, 160 ] },
        { name: 'Оценка Импортера', data: [100, 100, 120, 120, 140, 140, 160] },
        { name: 'Портфель 50%+50%', data: [100, 110, 120, 130, 140, 150, 160] },
    ];

    let options = {
        chart: {
            type: 'line',
            toolbar: {
                show: false,
            }
        },
        stroke: {
    //        width: 7,
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: [...new Array(7)].map((item, index) => { var d = new Date(); return d.setMonth(d.getMonth() - 6 + index) }),
            style: {
                color: "gray",
                fontSize: '1rem',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 200,
                cssClass: 'apexcharts-yaxis-title',
            }
        },
        // fill: {
        //     type: 'gradient',
        //     gradient: {
        //         shade: 'dark',
        //         gradientToColors: ['#FDD835'],
        //         shadeIntensity: 1,
        //         type: 'horizontal',
        //         opacityFrom: 1,
        //         opacityTo: 1,
        //         stops: [0, 100, 100, 100]
        //     },
        // },
        markers: {
            size: 2,
            colors: ["#D3D3D3"],
            strokeColors: "#808080",
            strokeWidth: 2,
            hover: {
                size: 4,
            }
        },
        yaxis:
        {
            min: 80,
            max: 180,
            title: {
                text: 'Доходность актива за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 5,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            }
        }
    }


    return <Container className="mt-3" >
        <Chart options={options} series={series} />
    </Container>

}

export default LineExportImportPortfolio;
